<script>
	import svgLogo from './views/svg.vue';

	export default {
		components: {
			'svg-logo': svgLogo
		},

		data() {
			return {
				actualColor: 'light',
				hoverClass: ''
			};
		},

		methods: {
			expandNav: function(event){
				if(event){
					event.target.closest('.nav-expand').classList.toggle('nav-expanded');
				};
			},

			changeColorLight: function(event){
				if(event){
					if(this.actualColor === 'monokai') {
						this.actualColor = "light";
						this.changeColorSelection();
						this.changeColorClass();
					};
				};
			},

			changeColorMonokai: function(event){
				if(event){
					if(this.actualColor === 'light') {
						this.actualColor = "monokai";
						this.changeColorSelection();
						this.changeColorClass();
					};
				};
			},

			changeColorSelection: function() {
				var selected = document.querySelector('.nav-colorbox .nav-selection');
				selected.classList.remove('nav-selection');
				event.target.classList.add('nav-selection');
			},

			changeColorClass: function() {
				var html = document.documentElement;
				html.className = this.actualColor;
			},

			drawCounter: function() {
				var list = document.querySelectorAll('.nbi-left');
				for (var i = 0; i < list.length; i++) {
					list[i].setAttribute('data-counter', i + 1);
				};
			}

		},

		mounted() {
			this.changeColorClass();
			this.drawCounter();
		}
	}
</script>