import Vue from 'vue';
import Router from 'vue-router';
import logomaker from './views/logomaker.vue';
import qip from './projects/qip.ru.vue';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: logomaker
    },
    {
      path: '/qip_ru',
      component: qip,
      props: { name: 'qip.ru'}
    },
    {
      path: '/file_qip_ru',
      component: qip,
      props: { name: 'file'}
    },
    {
      path: '/8march_qip_ru',
      component: qip,
      props: { name: '8march'}
    },
    {
      path: '/14_qip_ru',
      component: qip,
      props: { name: '14'}
    },
    {
      path: '/2011_qip_ru',
      component: qip,
      props: { name: '2011'}
    },
    {
      path: '/2012_qip_ru',
      component: qip,
      props: { name: '2012'}
    },
    {
      path: '/facebook_qip_ru',
      component: qip,
      props: { name: 'facebook'}
    },
    {
      path: '/qipsocial_qip_ru',
      component: qip,
      props: { name: 'qipsocial'}
    }
  ]
})
